@import url(https://fonts.googleapis.com/css?family=Rubik+Mono+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Rubik+Mono+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    background-color: black;
}
/* font-family: 'Rubik Mono One', sans-serif; */

.navbar {
    background-color: black;
    height: 120px;
    position: fixed;
    top: 0;
    width: 100%;
    font-family: 'Rubik Mono One', sans-serif;
}

.status, .score{
    color: white;
    text-shadow: -1px -1px 0 rgb(95, 141, 194), 1px -1px 0 rgb(95, 141, 194) , -1px 1px 0 rgb(95, 141, 194) , 1px 1px 0 rgb(95, 141, 194);
}

.status {
    margin-left: 200px;
}
/* font-family: 'Rubik Mono One', sans-serif; */

.background-pic {
    background: url(/clicky-game/static/media/all-doctors.aae3e056.jpg);
    background-size: 100%;
    height: 650px;
    background-repeat: no-repeat;
    
}

.jumbotron {
    background: rgba(72, 87, 110, 0.76);
    margin-top: 150px;
    width: 90%;
    height: 45%;
    border-radius: 90% / 45%;
    border: solid 2px white;
}

.desc-title {
    color: white;
    text-shadow: -3px -3px 0 rgb(30, 38, 58), 3px -3px 0 rgb(30, 38, 58) , -3px 3px 0 rgb(30, 38, 58) , 3px 3px 0 rgb(30, 38, 58) ;
    font-family: 'Rubik Mono One', sans-serif;
}

.description {
    color: white;
    text-shadow: -2px -2px 0 rgb(30, 38, 58), 2px -2px 0 rgb(30, 38, 58) , -2px 2px 0 rgb(30, 38, 58) , 2px 2px 0 rgb(30, 38, 58) ;
    font-family: 'Rubik Mono One', sans-serif;
}

.wrapper {
    padding-top: 50px;
    background: black;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto; 
  }

  .card-holder {
    margin-bottom: 50px;
  }
.card {
    margin: 5px;
}

body {
    background-color: black;
}
.footer {
    height: 90px;
    background-color: white;
}
